.Loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1000000000;
    font-family: "Josefin Sans", sans-serif;
    background: radial-gradient(#121212, #000);
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    transition: opacity 0.75s, visibility 0.75s;
    .cubeWrap {
        perspective: 800px;
        width: 300px;
        overflow: hidden;
        position: absolute;
        -webkit-animation: bouncy 2s ease forwards;
        animation: bouncy 2s ease forwards;

        .cube {
            margin: 8em auto;
            width: 6em;
            height: 6em;
            transform-style: preserve-3d;
            transform-origin: 50% 50% -3em;
            position: relative;
            -webkit-animation: spin 6s linear infinite;
            animation: spin 6s linear infinite;
            transform: rotateX(-50deg) rotateY(405deg);
        }
        div {
            position: absolute;
            width: 6em;
            height: 6em;
            transform-style: preserve-3d;
        }
    }
    .faces1::before,
    .faces1::after,
    .faces2::before,
    .faces2::after {
        position: absolute;
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        transform-style: preserve-3d;
    }

    .faces1::before,
    .faces2::before {
        transform: rotateY(90deg);
        transform-origin: 0 50%;
        background-color: purple;
    }

    .faces2 {
        transform: rotateX(180deg) translateZ(6em);
    }

    .faces2::before {
        background-color: orange;
        transform: rotateY(-90deg);
        transform-origin: 100% 50%;
    }
    .faces1::after,
    .faces2::after {
        transform: rotateX(-90deg);
        transform-origin: 50% 0;
        background-color: red;
    }

    .faces1 {
        background-color: #ffa500;
    }

    .faces2 {
        background-color: #ffa90a;
    }

    .faces1::before {
        background-color: #ffac14;
    }

    .faces2::before {
        background-color: #ffb329;
    }

    .faces1::after {
        background-color: #f59e00;
    }

    .faces2::after {
        background-color: #eb9800;
    }

    @-webkit-keyframes spin {
        0% {
            transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
        }
        100% {
            transform: rotateX(720deg) rotateY(360deg) rotateZ(0deg);
        }
    }

    @keyframes spin {
        0% {
            transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
        }
        100% {
            transform: rotateX(720deg) rotateY(360deg) rotateZ(0deg);
        }
    }
    @-webkit-keyframes bouncy {
        /* up */
        0% {
            transform: translateY(-600px) scale(0.8, 1.2);
            -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
        }
        /* floor */
        30% {
            transform: translateY(0) scale(0.8, 1.2);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
        /* floor */
        32% {
            transform: translateY(0) scale(1, 0.8);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
        /* up */
        45% {
            transform: translateY(-100px) scale(0.9, 1.1);
            -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
        }
        /* floor */
        60% {
            transform: translateY(0) scale(0.9, 1.1);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
        /* floor */
        62% {
            transform: translateY(0) scale(1, 0.9);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
        /* up */
        70% {
            transform: translateY(-30px) scale(0.9, 1.1);
            -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
        }
        /* floor */
        80% {
            transform: translateY(0) scale(1, 1);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
        /* floor */
        82% {
            transform: translateY(0) scale(1, 0.95);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
        /* up */
        85% {
            transform: translateY(-10px) scale(0.98, 1.02);
            -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
        }
        /* floor */
        90% {
            transform: translateY(0) scale(1, 1);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
        100% {
            transform: translateX(-900px) scale(0.8, 1.2);
            -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
        }
    }
    @keyframes bouncy {
        /* up */
        0% {
            transform: translateY(-600px) scale(0.8, 1.2);
            -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
        }
        /* floor */
        30% {
            transform: translateY(0) scale(0.8, 1.2);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
        /* floor */
        32% {
            transform: translateY(0) scale(1, 0.8);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
        /* up */
        45% {
            transform: translateY(-100px) scale(0.9, 1.1);
            -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
        }
        /* floor */
        60% {
            transform: translateY(0) scale(0.9, 1.1);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
        /* floor */
        62% {
            transform: translateY(0) scale(1, 0.9);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
        /* up */
        70% {
            transform: translateY(-30px) scale(0.9, 1.1);
            -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
        }
        /* floor */
        80% {
            transform: translateY(0) scale(1, 1);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
        /* floor */
        82% {
            transform: translateY(0) scale(1, 0.95);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
        /* up */
        85% {
            transform: translateY(-10px) scale(0.98, 1.02);
            -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
        }
        /* floor */
        90% {
            transform: translateY(0) scale(1, 1);
            -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
        }
        100% {
            transform: translateX(-900px) scale(0.8, 1.2);
            -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
        }
    }
}
.Loader-none {
    opacity: 0;
    visibility: hidden;
}
